<template>
  <div id="right">
    <h3>회원 정보를 수정할 수 있습니다.</h3>
    <p>수정사항</p>

    <label for="id">ID : </label>
    <input type="text" v-model="user.id" maxlength="20"> <br/>
    <label for="password">password : </label>
    <input type="text" v-model="user.password"> <br/>
    <label for="passwordCheck">password check: </label>
    <input type="text" v-model="user.passwordCheck"> <br/>
    <label for="E-mail">E-mail : </label>
    <input type="text" v-model="user.email"> <br/>
    <button @click="changeUser">회원가입</button>

  </div>
</template>

<script>
export default{
  props: ['id', 'password', 'passwordCheck', 'email'],
  data(){
    return{
      user: {}
    }
  },
  created(){
    // 재할당을 통해 user오브젝트로 넣어줌
    this.user.id = this.id
    this.user.password = this.password
    this.user.passwordCheck = this.passwordCheck
    this.user.email = this.email
  },
  methods: {
    changeUser(){
      console.log(this.user)
      this.$emit('child', this.user) //부모에게 요청할때
      // this.$emit('child', this.user,1,2,3,4)
      // child(this.user,1,2,3,4) 인자값이 여러개일경우
      alert('회원가입 되었습니다.')
    }
  }
}
</script>

<style scoped>
  #right{
    width: 50%;
    background: rgba(243, 243, 192, 0.808);
    float: left;
  }
  button{
    margin: 0.5rem;
  }
  input{
    margin-bottom: 0.3rem;
  }
  </style>
  