<template>
    <div>
        <h1>Welcome to {{title}}</h1>
        <input type="text" v-model="input1">
        <button @click="getData">Get</button>
        <button @click="setData">Set</button>
    </div>
</template>
<script>
export default {
    components: {},
    data() {
        return {
            title: '개발자의 미래',
            input1: 'abcd'
        }
    },
    setup() {},
    beforeCreate(){     //아직 데이터가 정의되기 전에 실행되는 구조이기 때문에 undefined 출력, 실제로 데이터가 생성되기 전이기 때문에 거의 의미가 없음.
        console.log('beforeCreate') //**단순히 어디서 작업이(리사이클이) 도는지 확인하기위해
    },
    created(){      //작성하는 순서와 관계없이 created가 먼저 만들어지고, 데이터가 출력됨.
        console.log('created') 
    },
    beforeMount(){      //mount가 되기전에 html의 내용을 검색할 수 없음.
        console.log('beforeMount') 
    },
    mounted() {     //template의 h1, input, button tag가 출력됨.
        console.log('mounted') 
    },
    beforeUpdate(){
        console.log('beforeUpdate') 
    },
    updated(){
        console.log('updated') 
    },
    beforeDestroy(){
        console.log('beforeDestroy') 
    },
    distroy(){
        console.log('distroy') 
    },
    unmounted() {},
    methods: {
        getData(){
            alert(this.input1)
        },
        setData(){
            this.input1 = '123456789'
        }
    }
}
</script>